<template>
  <div class="mt-4">
      <v-card-title>
      <div class="text-h5 font-weight-light">
        Pengkinian Data Karyawan
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
          width="100px"
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="data.data"
        loading-text="Loading... Please wait"
        :loading="data.loading"
        :search="search"
        class="elevation-1"
      >
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item)+1 }}.
        </template>
        <template #[`item.firstname`]="{ item }">
          {{ item.firstname }} {{ item.lastname }}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn  icon color="primary" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn  icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Detail/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Detail from '@/components/HCCombenupdate/Detail.vue'
export default {
  name: 'HCCombenupdate',
  components: {
    Detail
  },
  data: () => ({
    search: '',
    headers: [
      {
        sortable: false,
        text: '#',
        value: 'index'
      },
      {
        text: 'NIK',
        align: 'start',
        sortable: false,
        value: 'NIK'
      },
      { text: 'Nama', value: 'firstname' },
      { text: 'Jabatan', value: 'jobtitle' },
      { text: 'Lokasi Kerja', value: 'WorkLocation' },
      { text: 'JG', value: 'JG' },
      { text: 'PG', value: 'PG' },
      { text: 'Resign Date', value: 'ResignDate' },
      { text: 'Action', value: 'action' }
    ]
  }),
  mounted: function () {
    store.dispatch('hccombenupdate/reqData')
  },
  computed: {
    ...mapState('hccombenupdate', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('hccombenupdate', [
      'detail', 'edit'
    ])
  }
}
</script>
